@import "variables";
@import "mixins";

html {
    font-size: 0.9rem;
}
body {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background: url(../images/login_img_bg@2x.png) no-repeat center;
    background-size: cover;
}
.login {
    display: flex;
    flex-direction: column-reverse;
    margin-top: 20px;
    background-color: white;
    border-radius: Min(2.5rem, 3vw);
    overflow: hidden;
    margin-inline: 1rem;
    @include breakpoint($md) {
        margin-top: 40px;
    }

    @include breakpoint($lg) {
        flex-direction: row;
    }

    .start {
        margin-top: 30px;
        width: 100%;
        padding-inline: Min(7.5rem, 7vw);
        padding-block: Min(5.5rem, 5vw);
        @include breakpoint($md) {
            margin-top: 20px;
        }
        @include breakpoint($xxl) {
            padding-block: Min(5.5rem, 5vw);
            margin-top: 20px;
        }
    }
    .end {
        padding: 20px 0 0 0;
        flex-shrink: 0;
        display: flex;
        align-items: center;
        background-color: #4dc9c6;
        @include breakpoint($md) {
            width: 100%;
        }
        @include breakpoint($lg) {
            width: 30%;
        }
        @include breakpoint($xl) {
            width: 28%;
        }

        img {
            width: 150px;
                margin: 0 auto -30px auto;
            @include breakpoint($md) {
                width: 200px;
            }
            @include breakpoint($lg) {
                width: 250px;
                margin-left: -30px;
            }

            @include breakpoint($xl) {
                width: 300px;
                margin-left: -15%;
                width: 100%;
            }
        }
    }

    // a
    a {
        text-decoration: none;
    }

    // input
    input {
        border-radius: 1rem;
        padding: 0.7rem;
        font-size: 1.25rem;
        @include breakpoint($sm) {
            padding: 1rem;
            border-radius: 1.25rem;
        }
    }
    // title
    .title {
        font-weight: bold;
        color: #505050;
        text-align: center;
        font-size: 1.6rem;
        @include breakpoint($sm) {
            font-size: 2rem;
        }
        @include breakpoint($lg) {
            margin-inline: 0;
            font-size: Min(2.625rem, 5vw);
        }
        @include breakpoint($xl) {
            margin-inline: 0.5em;
        }
    }
    // 帳密
    .user-account,
    .user-password {
        padding-left: 4.375rem;
        background: no-repeat left 1em center;
    }

    // 帳號
    .user-account {
        background-image: url(../images/login_ic_user.svg);
    }

    // 密碼
    .user-password {
        padding-right: 4.375rem;
        background-image: url(../images/login_ic_password.svg);
    }
}

// 密碼眼睛
.password-eye {
    position: relative;
    .icon {
        position: absolute;
        top: 50%;
        right: 0;
        transform: translate(-100%, -50%);
    }
    a {
        position: relative;
        &::before {
            position: absolute;
            top: 50%;
            left: 50%;
            content: "";
            display: block;
            width: 2.5rem;
            height: 2.5rem;
            transform: translate(-50%, -50%);
        }
    }
}

// 登入按鈕
.btn-login {
    font-size: 1.5rem;
    border-radius: 1rem;
    padding: 0.7rem;
    width: 100%;
    @include breakpoint($sm) {
        padding: 1rem;
        font-size: 1.75rem;
        border-radius: 1.25rem;
    }
}

// 單位
.org-name {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    gap: 0.75rem;
    margin-top: 3.75rem;
    color: white;
    white-space: nowrap;
    font-size: 16px;
    margin-bottom: 3rem;
    @include breakpoint($sm) {
        margin-top: 2rem;
        margin-bottom: 2rem;
        font-size: 0.9rem;
    }
    .ch {
        font-size: 20px; //1.25rem
        letter-spacing: 0.125rem;
        font-weight: bold;
        @include breakpoint($sm) {
            font-size: 1.1rem;
        }
    }
}

.verification {
    @include breakpoint($md) {
        height: 50px;
    }
}
