// 斷點
@mixin breakpoint($size) {
    @media (min-width: $size) {
        @content;
    }
}
// 方形
@mixin rect($size, $color: currentColor) {
    width: $size;
    height: $size;
    background-color: $color;
}
// 圓形
@mixin circle($size, $color: currentColor) {
    @include rect($size);
    border-radius: 999rem;
    background-color: $color;
}
// 三角形
@mixin triangle-left($w, $h:$w, $color:currentColor){
    width: 0;
    height: 0;
    border-top: calc($h / 2) solid transparent;
    border-right: $w solid $color;
    border-bottom: calc($h / 2) solid transparent;
}
@mixin triangle-right($w, $h:$w, $color:currentColor){
    width: 0;
    height: 0;
    border-top: calc($h / 2) solid transparent;
    border-left: $w solid $color;
    border-bottom: calc($h / 2) solid transparent;
}
@mixin triangle-up($w, $h:$w, $color:currentColor){
    width: 0;
    height: 0;
    border-left: calc($w / 2) solid transparent;
    border-right: calc($w / 2) solid transparent;
    border-bottom: $h solid $color;
}
@mixin triangle-down($w, $h:$w, $color:currentColor){
    width: 0;
    height: 0;
    border-left: calc($w / 2) solid transparent;
    border-right: calc($w / 2) solid transparent;
    border-top: $h solid $color;
}

//自訂斷點
/*--溢出文字點點點--*/
@mixin over-text($line) {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: $line;
}

/*--隱藏區塊內文字--*/
@mixin text-indent {
    text-indent: -9999px;
    overflow: hidden;
    white-space: nowrap;
}
/* --漸層填色設定-- */
@mixin gradient-horizon($colorstart, $colorend) {
    background: $colorstart;
    /* Old browsers */
    background: -moz-linear-gradient(left, $colorstart 0%, $colorend 100%);
    /* FF3.6-15 */
    background: -webkit-linear-gradient(left, $colorstart 0%, $colorend 100%);
    /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to right, $colorstart 0%, $colorend 100%);
    /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
}